body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align:center;
}

/* navigation bar */

.nav {
  height: 50px;
}

.logo {
  float: left;
}

.nav-bar{
  padding-top: 15px;
  float: right;
}

.nav-bar a {
  padding: 25px 50px;
}

/* header */

.header {
  color: white;
  background-color: #ecae1c;
  padding: 200px 0;
}

/* body */

.body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  color: white;
  background-color: gray;
}

.body h4 {
  font-size: 1.4em;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
}

.img-responsive {
  padding: 20px;
}
.food-pics {
  padding-bottom:50px;
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
}

.history {
  text-align: center;
  line-height: 1.5;
  padding: 50px 100px;
  color: #8a8a8a;
  background-color: #f5f5f5;
}


/* footer */

.footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 200px;
  background-color: #D3D3D3;
}

.address {
  padding-top:25px;
  padding-left: 25px;
  -ms-flex-pack: center;
      justify-content: center;
}

.social-buttons {
-ms-flex-pack: center;
    justify-content: center;
}



